@import url(https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,100;0,300;0,400;0,700;1,400;1,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{padding:0;margin:0;box-sizing:border-box}html{width:100%;font-size:10px}body{font-family:"Lato", sans-serif}.App{transition:all 0.5s}.active-library{margin-left:30rem}.player{font-size:1.6rem;min-height:20vh;width:100%;display:flex;flex-direction:column;justify-content:space-between;align-items:center}.time-control{display:flex;align-items:center;justify-content:space-between;width:50%}.time-control input{width:100%;-webkit-appearance:none;background-color:transparent}.time-control p{padding:1rem}.play-control{display:flex;justify-content:space-between;width:30%}.track{background:lightblue;width:100%;height:1rem;position:relative;border-radius:1rem;overflow:hidden}.animated-track{background:#ccc;width:100%;height:100%;position:absolute;top:0;left:0;transform:translateX(0);pointer-events:none}input[type="range"]:focus{outline:none}input[type="range"]::-webkit-slider-thumb{-webkit-appearance:none;height:16px;width:16px;background:#000}@media screen and (max-width: 678px){.time-control{width:90%}.play-control{width:60%}}.song-container{font-size:1.6rem;min-height:60vh;width:100%;color:#9b9b9b;text-align:center;padding:4rem 0}.song-container h2{color:rgba(0,0,0,0.8)}.song-container h3{color:rgba(0,0,0,0.7);font-weight:500}.song-container img{width:20%;border-radius:50%}@media screen and (max-width: 678px){.song-container img{width:60%}}.library{font-size:1.6rem;font-weight:300;color:#9b9b9b;background-color:#fff;position:fixed;top:0;left:0;width:35rem;height:100%;box-shadow:2px 2px 50px #ccc;overflow:scroll;transform:translateX(-100%);transition:all 0.5s;opacity:0}.library h2{padding:2rem}.library-songs{display:flex;flex-direction:column;align-items:center}.library-songs img{width:30%}.library-song{display:flex;justify-content:flex-start;align-items:center;padding:1rem 2rem;cursor:pointer}.library-song:hover{background:#dedeff}.song-description{padding-left:2rem}.show{transform:translateX(0);opacity:1}.selected{background-color:#91a5da}*{scrollbar-width:thin;scrollbar-color:rgba(155,155,155,0.5) transparent}*::-webkit-scrollbar{width:5px}*::-webkit-scrollbar-track{background:transparent}*::-webkit-scrollbar-thumb{background-color:rgba(155,155,155,0.5);border-radius:20px;border:transparent}@media screen and (max-width: 678px){.library{width:100%}}.nav-container{font-size:1.6rem;min-height:10vh;display:flex;justify-content:space-around;align-items:center}.nav-container button{padding:1rem;background-color:transparent;border:2px solid #414141;outline:none;cursor:pointer;transition:background-color 0.5s}.nav-container button:hover{background-color:#414141;color:#fff}@media screen and (max-width: 678px){button{z-index:100}}

